import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { ColEditDelete } from 'components/common/index';
import { Card, Row, Col, message, Table } from 'antd';
// import { getQueryParams } from 'utils/urlUtil';
import FormIntegrateTraining from './FormIntegrateTrainning';
import { deleteDocIn } from '../../services/docIn';
import { dateToString } from '../../utils/formatUtil';

class ListIntegratedTraining extends Component {
  state = {
    reload: 0,
    // paramSearch: {},
  };

  // componentDidMount = () => {
  //   this.handleChangeQueryParams();
  // };

  // componentWillReceiveProps = () => {
  //   this.handleChangeQueryParams();
  // };

  // handleChangeQueryParams = () => {
  //   this.setState({ paramSearch: getQueryParams() });
  // };

  handleDelete = async id => {
    await deleteDocIn(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportDocOut = () => {
    const { dataTable } = this.state;
    console.log('dataTable', dataTable);
    // downloadFile(dataTable);
  };

  dataTable = data => {
    this.setState({
      dataTable: data,
    });
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'docInNo',
        key: 'docInNo',
      },
      {
        title: 'Họ Tên',
        dataIndex: 'docInDate',
        key: 'docInDate',
        render: text => dateToString(text),
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'sentPlace',
        key: 'sentPlace',
      },
      {
        title: 'PCT',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tổng điểm',
        dataIndex: 'noteDate',
        key: 'noteDate',
        render: text => dateToString(text),
      },
      {
        title: 'Kỷ Luật',
        dataIndex: 'docContent',
        key: 'docContent',
      },
      {
        title: 'Xếp loại rèn luyện',
        dataIndex: 'savedPlace',
        key: 'savedPlace',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`${this.urlFormEdit}/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <FormIntegrateTraining />
        <Row>
          <Col span={24}>
            <Table columns={columns} rowKey="id" dataTable={[]} />
            {/* <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              formSearch
              service={getDocIns}
              dataTable={this.dataTable || []}
              columns={columns}
              rowKey="id"
            /> */}
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListIntegratedTraining {...props} />
  </Page>
));



